/*----------- Layout -----------*/

/* Layout construction */

@base: {
  max-width: 960px;
  columns-name: col-;
  columns-total: 12;
  columns-halfgap: false;
  aperture: 1rem;
  marge: 1rem;
}

/* Media definition for media queries and grid. */

@media: {
  tablet: 760px;
  mobile: 500px;
}

@zindex: {
  dropdown: 1000;
  sticky: 1020;
  fixed: 1030;
  modal-backdrop: 1040;
  modal: 1050;
  popover: 1060;
  tooltip: 1070;
}

/**
 * For each font used; set up the different weight
 * and value. Each font need a new .font-weight
 * definition.
 */

@fonts: {
  @primary: {
    @name: 'Sans-serif';
    @weight: {
      @thin: 100;
      @extra-light: 200;
      @light: 300;
      @regular: 400;
      @bold: 500;
      @extra-bold: 700;
      @black: 900;
    }
  }

  @secondary: {
    @name: 'monospace';
    @weight: {
      @thin: 100;
      @extra-light: 200;
      @light: 300;
      @regular: 400;
      @bold: 500;
      @extra-bold: 700;
      @black: 900;
    }
  }
}

/*----------- Typography -----------*/

/**
 * About line height : Reference : https://kittygiraudel.com/2020/05/18/using-calc-to-figure-out-optimal-line-height
 *
 */

@typography: {
  site-font-size: 16px;
  base-font-size: 1.065rem;
  /* Calculated line height.
  Reference : https://kittygiraudel.com/2020/05/18/using-calc-to-figure-out-optimal-line-height/ */
  base-line-height: calc(4px + 2ex + 4px);
  /* Margin bottom is 65% of the font size */
  base-margin: 0.65em;
  base-type-ratio: @type-ratios[perfectFourth];
  mobile-type-ratio: @type-ratios[majorThird];
}

@headings: {
  margin-top: 1em;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
  font-weight: bold;

  @h1: {
    font-weight: @headings[font-weight];
  }

  @h2: {
    font-weight: @headings[font-weight];
  }

  @h3: {
    font-weight: @headings[font-weight];
  }

  @h4: {
    font-weight: @headings[font-weight];
  }

  @h5: {
    font-weight: @headings[font-weight];
  }

  @h6: {
    font-weight: normal;
  }
}

@paragraph-sizes: {
  pearl: -2;
  small-print: -1;
  cicero: 0;
  lead: 1;
  headline: 2;
  parangon: 3;
  canon: 4;
}

@type-ratios: {
  minorSecond: 1.067;
  majorSecond: 1.125;
  minorThird: 1.2;
  majorThird: 1.25;
  perfectFourth: 1.333;
  augmentedFourth: 1.414;
  perfectFifth: 1.5;
  goldenRatio: 1.618;
}

/*----------- Colors -----------*/

// Remove color scheme managment

@color-scheme: {
  dark: false
}

// Depreciated

@hues: {
  primary: 0;
  secondary: 220;
  success: 103;
  danger: 358;
  warning: 127;
  info: 127;
}

