@colors: {
    // switch to "monochromatic"

  @light: {
    primary: hsl(@hues[primary]; 81%; 58%);
    secondary: hsl(@hues[secondary]; 100%; 68%);
    text: hsl(@hues[primary]; 4%; 26%);
    border: hsl(@hues[primary]; 0%; 90%);
    background: hsl(@hues[primary]; 0%; 100%);
    primaryOffset: hsl(@hues[primary]; 100%; 68%);
    textOffset: hsl(@hues[primary]; 90%; 70%);
    backgroundOffset: hsl(@hues[primary]; 6%; 100%);

    success: hsl(@hues[success]; 100%; 78%);
    danger: hsl(@hues[danger]; 100%; 56%);
  }

  @dark: {
    primary: hsl(@hues[primary]; 81%; 58%);
    secondary: hsl(@hues[secondary]; 100%; 68%);
    text: hsl(@hues[primary]; 4%; 92%);
    border: hsl(@hues[primary]; 0%; 30%);
    background: hsl(@hues[primary]; 0%; 15%);
    primaryOffset: hsl(@hues[primary]; 100%; 68%);
    textOffset: hsl(@hues[primary]; 90%; 70%);
    backgroundOffset: hsl(@hues[primary]; 6%; 100%);

    success: hsl(@hues[success]; 100%; 78%);
    danger: hsl(@hues[danger]; 100%; 56%);
  }
}

/*----------- Color Sets -----------*/

@set1: {
  @primary: #3d57eb;
  @secondary: spin(#3d57eb, -(360 / 3));
  @thirdary: spin(#3d57eb, (360 / 3));
}

@tints: {

  @tonic: {
    operation: saturate;
    amount: 10%;
    method: relative;
  }
  @ultratonic: {
    operation: saturate;
    amount: 20%;
    method: relative;
  }
  @dark: {
    operation: darken;
    amount: 80%;
    method: relative;
  }
  @gloom: {
    operation: darken;
    amount: 60%;
    method: relative;
  }
  @dusk: {
    operation: darken;
    amount: 40%;
    method: relative;
  }
  @major: {
    operation: tint;
    amount: 0%;
    method: false;
  }
  @mediant: {
    operation: lighten;
    amount: 20%;
    method: false;
  }
  @sensible: {
    operation: lighten;
    amount: 25%;
    method: false;
  }
  @minor: {
    operation: lighten;
    amount: 30%;
    method: false;
  }
  @brittle: {
    operation: lighten;
    amount: 40%;
    method: false;
  }
}
